/*ESTRUTURA*/

body {
	margin: 0;
	padding: 0;
	background-color: #fcfcfc;
}

* {
	font-family: Helvetica, Arial, sans-serif;
	font-weight: 300;
}

.App {
	width: 100%;
	height: 90vh;
}

.Opcoes {
	margin: 10px auto;
}

.text-center {
	text-align: center;
}

.padding-20 {
	padding: 0 20px;
}

/*CONTAINER*/

.flex {
	display: -ms-flexbox;
	display: flex;
}

.vertical {
	-ms-flex-direction: column;
	    flex-direction: column;
}

.horizontal {
	-ms-flex-direction: row;
	    flex-direction: row;
}

.flex-center {
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

/*CORES*/

.switch.on.orange {
	background-color: orange;
}

.switch.on.red {
	background-color: red;
}


